import React, { useState } from "react";
import "./Onglets.css";
import "./Slide.css";

import SlideshowChakras from "./SlideChakras";
import SlideshowLyto from "./SlideLyto";

import productsLyto from "./products/ProductsLyto";
import productsChakras from "./products/ProductsChakras";

export default function Onglets() {
  const [stateOnglets, setStateOnglets] = useState(1);

  const go1 = () => {
    setStateOnglets(1);
  };

  const go2 = () => {
    setStateOnglets(2);
  };


  return (
    <div className="contain">
      <div className="contBtn">
        <div
          onClick={go1}
          className={`onglets ${stateOnglets === 1 ? "active" : ""}`}
        >
          {" "}
          <h1>Pendentifs arbre de vie lytothérapie.</h1>
        </div>
        <div
          onClick={go2}
          className={`onglets ${stateOnglets === 2 ? "active" : ""}`}
        >
          <h1>Pendentifs 7 chakras..</h1>
        </div>
       
      </div>
      <div className="container">
        { stateOnglets === 1 ? (
          <div className="cont">
            <section className="slideshowLyto">
              {productsLyto.map(product => (
                <SlideshowLyto
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ):(
          <div className="cont">
            <section className="slideshowChakras">
              {productsChakras.map(product => (
                <SlideshowChakras
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) 
      }
      </div>
    </div>
  );
}
